import tabControl from '@/components/tabControl-tag/tabControl-tag.vue';
export default {
	components: {
		tabControl
	},
	data() {
		return {
			current: 0,
			items: ['待确认', '已确认', '已取消'],
			a: [],
			b: [],
			c: [],
			itemList:[{
				
			},{
				
			},{
				
			}],
			store_id: 0
		}
	},
	onLoad:function(option){
		this.store_id = option.shopid
	},
	onShow:function(){
		var that = this
		uni.request({
			url: this.$bashUrl+ '/index.php/home/Api/new_orders/',
			method: 'POST',
			data:{
				store_id: this.store_id,
			},
			success:(res=>{
				console.log(res.data)
				for(var i=0;i<res.data.length;i++){
					if(res.data[i].state==0){
						that.a.push(res.data[i])
					}else if(res.data[i].state==1){
						that.b.push(res.data[i])
					}else if(res.data[i].state==2){
						that.c.push(res.data[i])
					}
				}
				console.log(that.a)
			}),fail:(err=>{
				console.log(err)
			}) 
		})
	},
	methods: {
		onClickItem:function(val) {
			this.current = val.currentIndex
			console.log("点击了事件"+val.currentIndex)
		},
		// 打电话
		callphone:function(e){
			var phone = e.currentTarget.dataset.id
			uni.makePhoneCall({
			    phoneNumber: phone
			});
		},
		// 订单确认
		confirmAnorder:function(value){
			var that = this
			uni.request({
				url: this.$bashUrl+ '/index.php/home/Api/query_order/',
				method: 'POST',
				data:{
					// type:1为确认，type:2为完成
					type: 1,
					order_id: value
				},
				success:(res=>{
					uni.request({
						url: this.$bashUrl+ '/index.php/home/Api/new_orders/',
						method: 'POST',
						data:{
							store_id: this.store_id,
						},
						success:(res=>{
							that.a = []
							that.b = []
							that.c = []
							for(var i=0;i<res.data.length;i++){
								if(res.data[i].state==0){
									that.a.push(res.data[i])
								}else if(res.data[i].state==1){
									that.b.push(res.data[i])
								}else if(res.data[i].state==2){
									that.c.push(res.data[i])
								}
							}
						}),fail:(err=>{
							console.log(err)
						}) 
					})
				}),fail:(err=>{
					console.log('失败');
				}) 
			})
			uni.showToast({
				title: '已确认通过',
				icon:'none'
			});
		},
		// 订单已完成
		orderCompleted:function(value,index){
			// 删除指定元素
			var that = this
			uni.request({
				url: this.$bashUrl+ '/index.php/home/Api/query_order/',
				method: 'POST',
				data:{
					// type:1为确认，type:2为完成
					type: 2,
					order_id: value
				},
				success:(res=>{
					that.a = []
					that.b = []
					that.c = []
					uni.request({
						url: this.$bashUrl+ '/index.php/home/Api/new_orders/',
						method: 'POST',
						data:{
							store_id: this.store_id,
						},
						success:(res=>{
							console.log(res.data)
							for(var i=0;i<res.data.length;i++){
								if(res.data[i].state==0){
									that.a.push(res.data[i])
								}else if(res.data[i].state==1){
									that.b.push(res.data[i])
								}else if(res.data[i].state==2){
									that.c.push(res.data[i])
								}
							}
						}),fail:(err=>{
							console.log(err)
						}) 
					})
				}),fail:(err=>{
					console.log('失败');
				})
			})
			uni.showToast({
				title: '订单已确认完成',
				icon:"none"
			});
		}
	}
}
